import React, { useState, useEffect } from "react";
import { loginUrl, getTokenFromUrl, getLikedSongs } from "./spotify";
import "./App.css";

const App = () => {
  const [token, setToken] = useState("");
  const [songs, setSongs] = useState([]);
  const [pairs, setPairs] = useState([]);
  const [currentPair, setCurrentPair] = useState([null, null]);
  const [progress, setProgress] = useState(0);
  const [showRankings, setShowRankings] = useState(false);

  useEffect(() => {
    const hash = getTokenFromUrl();
    window.location.hash = ""; 
  
    const _token = hash.access_token;
    if (_token) {
      setToken(_token);
      getLikedSongs(_token).then((songs) => {
        setSongs(songs.map((song) => ({ ...song, compared: false })));
        setProgress(0);
        const initialPairs = generatePairs(songs);
        setPairs(initialPairs);
        pickRandomPair(initialPairs);
      });
    }
  }, []);

  useEffect(() => {
    const totalSongs = songs.length;
    const comparedSongs = songs.filter((song) => song.compared).length;
    const percentage = (comparedSongs / totalSongs) * 100;
    setProgress(percentage);
  }, [songs]);

  const generatePairs = (songs) => {
    const pairs = [];
    for (let i = 0; i < songs.length - 1; i++) {
      for (let j = i + 1; j < songs.length; j++) {
        pairs.push([songs[i], songs[j]]);
      }
    }
    return pairs;
  };

  const pickRandomPair = (pairs) => {
    const remainingPairs = pairs.filter(
      (pair) => !pair[0].compared || !pair[1].compared
    );
    if (remainingPairs.length === 0) {
      setShowRankings(true);
      return;
    }
    const randomIndex = Math.floor(Math.random() * remainingPairs.length);
    setCurrentPair(remainingPairs[randomIndex]);
    setPairs(pairs.filter((pair) => pair !== remainingPairs[randomIndex]));
  };

  const handleChoice = (winner, loser) => {
    const updatedSongs = songs.map((song) =>
      song.track.id === winner.track.id
        ? { ...song, compared: true, wins: (song.wins || 0) + 1 }
        : song
    );
    setSongs(updatedSongs);
    pickRandomPair(pairs);
  };

  const handleTie = () => {
    const updatedSongs = songs.map((song) => {
      if (
        song.track.id === currentPair[0].track.id ||
        song.track.id === currentPair[1].track.id
      ) {
        return { ...song, compared: true, wins: (song.wins || 0) + 1 };
      }
      return song;
    });
    setSongs(updatedSongs);
    pickRandomPair(pairs);
  };

  const showRankedSongs = () => {
    setShowRankings(true);
  };

  const rankSongs = () => {
    const ranked = songs.sort((a, b) => (b.wins || 0) - (a.wins || 0));
    let rank = 1;
    ranked.forEach((song, index) => {
      if (index > 0 && (song.wins || 0) !== (ranked[index - 1].wins || 0)) {
        rank = index + 1;
      }
      song.rank = rank;
    });
    return ranked;
  };

  return (
    <div className="App">
      <div className="header">Song Comparison</div>

      {!token ? (
        <a href={loginUrl}>Login to Spotify</a>
      ) : (
        <>
          <div className="total-liked-songs">
            Total Liked Songs: {songs.length}
          </div>

          {showRankings ? (
            <div className="ranked-songs">
              <h2>Ranked Songs</h2>
              <table>
                <thead>
                  <tr>
                    <th>Rank</th>
                    <th>Song Name</th>
                  </tr>
                </thead>
                <tbody>
                  {rankSongs().map((song) => (
                    <tr key={song.track.id}>
                      <td>{song.rank}</td>
                      <td>{song.track.name}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <>
              {currentPair[0] && currentPair[1] ? (
                <>
                  <div className="song-container">
                    <div
                      className="song"
                      onClick={() =>
                        handleChoice(currentPair[0], currentPair[1])
                      }
                    >
                      <img
                        src={currentPair[0].track.album.images[0].url}
                        alt={currentPair[0].track.name}
                      />
                      <div>{currentPair[0].track.name}</div>
                    </div>
                    <div
                      className="song"
                      onClick={() =>
                        handleChoice(currentPair[1], currentPair[0])
                      }
                    >
                      <img
                        src={currentPair[1].track.album.images[0].url}
                        alt={currentPair[1].track.name}
                      />
                      <div>{currentPair[1].track.name}</div>
                    </div>
                  </div>

                  <div className="progress-container">
                    <div className="progress-bar">
                      <div
                        className="progress"
                        style={{ width: `${progress}%` }}
                      >
                        {Math.round(progress)}%
                      </div>
                    </div>
                  </div>

                  <div className="buttons">
                    <div className="buttons-container">
                      <button onClick={handleTie}>Can't Decide</button>
                    </div>
                    <button onClick={showRankedSongs}>Show Ranked Songs</button>
                  </div>
                </>
              ) : (
                <div>Loading...</div>
              )}
            </>
          )}
        </>
      )}
    </div>
  );
};

export default App;
